import BigNumber from 'bignumber.js'
import { Sushi } from './sushi'
import { getFarms, getTotalLPWethValue, getMasterChefContract, getWethContract, getStaked, approve, stake } from './sushi/utils'
import { getContract, getAllowance } from '@/utils/erc20'
import conf from '@/config'
import { getBalance } from '../utils/erc20'
import bnp from './contract'
import config from '@/config'

// const bnp = new Sushi(conf.provider, conf.networkID, conf.debug, {
//   defaultAccount: conf.provider.selectedAddress,
//   defaultConfirmations: 1,
//   autoGasMultiplier: 1.5,
