// const bnp = contract
window.BNP = bnp
export { bnp }




const BLOCKS_PER_YEAR = new BigNumber(2336000)
const SUSHI_PER_BLOCK = new BigNumber(1000)

export default {
  namespaced: true,
  state: {
    blockNumber: 0,
    totalSupply: new BigNumber(0),
    stakedValue: [],
    pools: bnp.pools || [],
    bnpBalanceOf: new BigNumber(0),
    uniBalanceOf: new BigNumber(0),
    sushiBalanceOf: new BigNumber(0),
    ethBalanceOf: new BigNumber(0),
  },
  getters: {
    totalWethAmount: state => BigNumber.sum.apply(null, state.stakedValue.map(({wethAmount}) => wethAmount)),
    sushiPrice: state => {
      const sushiIndex = state.pools.findIndex(
        ({ tokenSymbol }) => tokenSymbol === conf.id,
      )
      const sushiPrice = sushiIndex >= 0 && state.stakedValue[sushiIndex]
        ? state.stakedValue[sushiIndex].tokenPriceInWeth
        : new BigNumber(0)
      return sushiPrice
    },
    menuList: (state, getters) => {
      return state.pools.reduce(
        (farmRows, farm, i) => {
          const farmWithStakedValue = {
            ...farm,
            ...state.stakedValue[i],
            apy: state.stakedValue[i]
              ? getters.sushiPrice
                .times(SUSHI_PER_BLOCK)
                .times(BLOCKS_PER_YEAR)
                .times(state.stakedValue[i].poolWeight)
                .div(state.stakedValue[i].totalWethValue)
              : null,
          }
          if (farmWithStakedValue.apy && farmWithStakedValue.apy.isNaN()) {
            farmWithStakedValue.apy = new BigNumber(0)
          }
          return [...farmRows, farmWithStakedValue]
        },
        [],
      )
    },
    harvest: state => [
      {
        id: 'bnp',
        ico: require('../assets/BNP.png'),
        num: state.bnpBalanceOf,
        harvest: !!state.bnpBalanceOf.toNumber(),
      },
      {
        id: 'uni',
        ico: require('../assets/UNI.png'),
        num: state.uniBalanceOf,
        harvest: !!state.uniBalanceOf.toNumber(),
      },
      {
        id: 'sushi',
        ico: require('../assets/SUSHI.png'),
        num: state.sushiBalanceOf,
        harvest: !!state.sushiBalanceOf.toNumber(),
      },
    ]
  },
  watch: {
    blockNumber() {
      this.fetchAllTokenBalanceOf()
    }
  },
  actions: {
    resetConnect() {
      bnp.setProvider(null)
      bnp.setAccount('')
    },
    onInit() {
      setInterval(() => {
        if (this.$root.account.isLogin) {
          bnp.web3.eth.getBlockNumber((err, n) => {
            if (!err && n != this.blockNumber) {
              this.blockNumber = n
            }
          })
        }
      }, 1000)
      // bnp.web3.eth.subscribe('accountsChanged', e => {
      //   console.log('accountsChangedaccountsChangedaccountsChangedaccountsChangedaccountsChangedaccountsChangedaccountsChangedaccountsChangedaccountsChangedaccountsChangedaccountsChanged')
      // })
    },
    onConnect(provider, account) {
      bnp.setProvider(provider, conf.networkID)
      bnp.setAccount(account || provider.selectedAddress)
      this.fetchAllTokenBalanceOf()
      this.bnpBalanceOf = new BigNumber(0)
      this.uniBalanceOf = new BigNumber(0)
      this.sushiBalanceOf = new BigNumber(0)
      this.ethBalanceOf = new BigNumber(0)
      this.events(provider)
    },
    async fetchAllTokenBalanceOf() {
      const balances = await Promise.all([
        bnp.bnp.balanceOf().catch(() => this.bnpBalanceOf),
        bnp.uniToken.balanceOf().catch(() => this.uniBalanceOf),
        bnp.sushiToken.balanceOf().catch(() => this.sushiBalanceOf),
        bnp.multicall.ethBalance().catch(() => this.ethBalanceOf)
      ])

      this.bnpBalanceOf = balances[0]
      this.uniBalanceOf = balances[1]
      this.sushiBalanceOf = balances[2]
      this.ethBalanceOf = balances[3]

      return balances
    },
    events(provider) {
      
      provider.on('accountsChanged', e => {
        console.log(e)
        this.$root.account.address = e[0]
      })
      provider.on('networkChanged', () => {
        window.location.reload()
      })

    },
    async fetchTotalSupply() {
      const num = await bnp.sushi.totalSupply()
      this.totalSupply = num
    },
    async fetchPools() {
      // this.pools = getFarms(bnp)
      await this.fetchAllStakedValue()
    },
    async fetchTokenBalance(tokenAddress) {
      // const provider = bnp.provider
      // const account = this.$root.account.address
      // const contract = new bnp.ERC20(bnp.web3, config.abi.ERC20Abi)
      // contract.setProvider(tokenAddress)
      // contract.setAccount(account)
      const balance = await this.getERC20Contract(tokenAddress).balanceOf()
      return new BigNumber(balance)
    },
    getERC20Contract(tokenAddress) {
      const account = this.$root.account.address
      const contract = new bnp.ERC20(bnp.web3, config.abi.ERC20Abi)
      contract.setProvider(tokenAddress)
      if (account) {
        contract.setAccount(account)
      }
      return contract
    },
    async fetchStakedValue(pid) {
      // const account = this.$root.account.address
      // const masterChefContract = getMasterChefContract(bnp);
      // // const 
      // const balance = await getStaked(masterChefContract, pid, account);
      // return new BigNumber(balance)
      const {amount} = await bnp.masterChef.userInfo(pid)

      return bnp.masterChef.balanceNum(amount)
    },
    async fetchAllStakedValue() {
      const masterChefContract = bnp.masterChef.contract;
      const wethContact = bnp.weth.contract;

      for (let i in this.pools) {
        let {pid, lpContract, tokenContract} = this.pools[i]
        console.log(tokenContract)
        try {
          const value = await getTotalLPWethValue(masterChefContract, wethContact, lpContract.contract, tokenContract.contract, pid)
          this.stakedValue[i] = value
        } catch(e){}
        this.stakedValue = [...this.stakedValue]
      }
      // this.pools.forEach(async ({ pid, lpContract, tokenContract, }) => {
      //   console.log(tokenContract)
      //   const value = await getTotalLPWethValue(masterChefContract, wethContact, lpContract.contract, tokenContract.contract, pid)
      //   this.stakedValue = [...this.stakedValue, value]
      // })

      // const balances = await Promise.all(this.pools.map(({ pid, lpContract, tokenContract, }) => getTotalLPWethValue(masterChefContract, wethContact, lpContract, tokenContract, pid)));
      // this.stakedValue = balances
    },
    getLpContract(address) {
      // const provider = bnp.provider
      // return getContract(provider, address)
      return this.getERC20Contract(address)
    },
    async fetchLPAllowance(tokenAddress) {
      // const masterChefContract = getMasterChefContract(bnp);
      // const account = this.$root.account.address
      // const allowance = await getAllowance(lpContract, masterChefContract, account)
      // // console.log(account, allowance)
      // return new BigNumber(allowance)
      const masterChefAddress = bnp.masterChef.address
      const lp = this.getERC20Contract(tokenAddress)
      const allowance = await lp.allowance(masterChefAddress)
      return new BigNumber(allowance)
    },
    async lpApprove(lpAddress) {
      // const masterChefContract = getMasterChefContract(bnp);
      // const account = this.$root.account.address
      // const tx = await approve(lpContract, masterChefContract, account)
      // return tx
      const masterChefAddress = bnp.masterChef.address
      const lp = this.getERC20Contract(lpAddress)
      return lp.approve(masterChefAddress)
    },
    async lpStake(pid, amount) {
      return bnp.masterChef.deposit(pid, amount)
    },
    async lpUnStake(pid, amount) {
      return bnp.masterChef.withdraw(pid, amount)
    },
    async fetchHarvest(pid) {
      // const r = await Promise.all([
      //   bnp.masterChef.pendingSushi(pid).catch(() => 0), // BNP & SUSHI
      //   // bnp.masterChef.pendingSushi(pid).catch(() => 0), // UNI
      //   // bnp.masterChef.pendingSushi(pid).catch(() => 0), // SUSHI
      // ])
      // console.log(r[0][0])
      // return {
      //   BNP: bnp.masterChef.balanceNum(r[0][1]),
      //   UNI: bnp.masterChef.balanceNum(r[1][1]),
      //   SUSHI: bnp.masterChef.balanceNum(r[0][0]),
      // }
      const r = await bnp.masterChef.pendingSushi(pid).catch(() => 0)
      return {
        BNP: bnp.masterChef.balanceNum(r[0]),
        UNI: new BigNumber(0),
        SUSHI: bnp.masterChef.balanceNum(r[1]),
      }
    },
    harvestBnp(pid) {
      return bnp.masterChef.deposit(pid, '0')
    },
    harvestUni(pid) {
      return bnp.masterChef.deposit(pid, '0')
    },
    harvestSushi(pid) {
      return bnp.masterChef.deposit(pid, '0')
    },
    // async stake(pid, amount) {
    //   const masterChefContract = getMasterChefContract(bnp);
    //   const account = this.$root.account.address
    //   console.log(pid, amount, masterChefContract)
    //   const tx = await stake(masterChefContract, pid, amount, account)
    //   console.log(tx)
    //   return tx
    // }
  }
}