import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    name: 'Home',
    component: Home
  },
  {
    path: '/menu',
    name: 'menu',
    component: () => import(/* webpackChunkName: "about" */ '../views/menu.vue')
  },
  {
    path: '/menu/pool/:id',
    name: 'menu-pool',
    component: () => import(/* webpackChunkName: "about" */ '../views/menu-pool.vue')
  },
  {
    path: '/exchange',
    name: 'exchange',
    component: () => import(/* webpackChunkName: "about" */ '../views/exchange'),
    children: [
      {
        path: '',
        redirect: 'swap'
      },
      {
        path: 'swap',
        name: 'exchange-swap',
        component: () => import(/* webpackChunkName: "about" */ '../views/exchange/swap.vue'),
      },
      {
        path: 'pool',
        name: 'exchange-pool',
        component: () => import(/* webpackChunkName: "about" */ '../views/exchange/swap.vue'),
      },
      {
        path: 'reward',
        name: 'exchange-reward',
        component: () => import(/* webpackChunkName: "about" */ '../views/exchange/swap.vue'),
      },
      {
        path: 'bridge',
        name: 'exchange-bridge',
        component: () => import(/* webpackChunkName: "about" */ '../views/exchange/swap.vue'),
      },
    ]

  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
