let config = null

Object.defineProperty(module, 'exports', {
  get() {
    if (config) {
      return config
    }
    const conf = (window.__CONFIG__ || {})
    if (!conf.networkID) {
      conf.networkID = 42
    }
    let envConfig
    switch(conf.networkID) {
      case 1: envConfig = prodConfig;break;
      case 3: envConfig = ropsten3;break;
      case 42: envConfig = kovan2;break;
      default: envConfig = ropsten3;break;
    }
    config = {
      ...{
        provider: evm[conf.networkID],
        networkID: conf.networkID,
      },
      ...defaultConfig,
      ...envConfig,
      ...conf,
    }
    config.tokenAddress.weth = WETH_ADDRESS[conf.networkID]
    config.tokenAddress.multiCall = MULTICALL_NETWORKS[conf.networkID]
    config.tokenAddress.uniToken = UNI_ADDRESS[conf.networkID]
    console.log(config)
    return config
  }
})

