import { ETHER, Token } from '@uniswap/sdk'
import { isAddress, tryParseAmount } from '@/utils/exchange'
import { getContract } from '@/utils/erc20'
import config from '@/config'
import { bnp } from './bnp'
import { getFarms, getTotalLPWethValue, getMasterChefContract, getWethContract, getStaked, approve, stake, getRouterContract, getETHBalance } from './sushi/utils'
import { BigNumber } from './sushi'
import { toBalanceNumber } from '@/utils/formatBalance'

export default {
  namespaced: true,
  state: {
    tokens: [
      {
        chainId: 1,
        decimals: 18,
        name: "Ether",
        symbol: "ETH",
        logoURI: require("../assets/ETH.png")
      },
      {
        address: "0x9B8EaA8837cF33167Cd5E44e8622B803cD96FE96",
        chainId: 1,
        name: "Binan",
        symbol: "BNP",
        decimals: 18,
        logoURI: require("../assets/BNP.png"),
      },
      {
        address: "0x9B8EaA8837cF33167Cd5E44e8622B803cD96FE96",
        chainId: 1,
        name: "WrappedEther",
        symbol: "USDT",
        decimals: 18,
        logoURI: require("../assets/USDT.svg"),
      },
      {
        address: "0xE158a25Ce4F551317ed461891e4419F6Cf035fC3",
        chainId: 1,
        name: "UNI",
        symbol: "UNI",
        decimals: 6,
        logoURI: require("../assets/UNI.svg"),
      },
      {
        address: "0x7E9b3C1ef89C790bbBec36EfB9c007dba539728E",
        chainId: 1,
        name: "Sushi Swap",
        symbol: "SUSHISWAP",
        decimals: 6,
        logoURI: require("../assets/SUSHI.png"),
      },
    ],
  },
  actions: {
    getToken(symbol) {
      return this.tokens.find(x => x.symbol == symbol)
    },
    async swapTokenInfo(symbol) {
      const token = this.getToken(symbol)
      const address = isAddress(token.address)
      const account = this.$root.account.address

      const tokenContract = getContract(bnp.provider, address)
      const routerContract = getRouterContract(bnp)

      const res = await Promise.all(token.symbol == 'ETH' ? [
        new BigNumber(0),
        getETHBalance(bnp, account).catch(() => 0).then(n => toBalanceNumber(new BigNumber(n))),
      ] : [
        tokenContract.methods.allowance(account, routerContract.options.address).call().catch(() => 0).then(n => toBalanceNumber(new BigNumber(n))),
        tokenContract.methods.balanceOf(account).call().catch(() => 0).then(n => toBalanceNumber(new BigNumber(n))),
      ])
      console.log(res)
      return {
        ...token,
        allowance: res[0],
        balance: res[1],
      }
    },
    getCurrency(token) {
      if (token.symbol == 'ETH') {
        return ETHER
      }
      const address = isAddress(token.address)
      const tokenContract = getContract(bnp.provider, address)

      return new Token(
        config.networkID,
        address,
        token.decimals,
        token.symbol,
        token.name,
      )
    },
    getCurrencyContract(token) {
      const address = isAddress(token.address)
      const tokenContract = getContract(bnp.provider, address)
      return tokenContract
    },
    async getCurrencyBalance(token, account) {
      const contract = this.getCurrencyContract(token)
      const balance = await contract.methods.balbaceOf(account).call()
      return balance
    },
    getTrade() {
      const inputCurrency = this.getCurrency(this.getToken(this.swap.from.coin))
      const outputCurrency = this.getCurrency(this.getToken(this.swap.to.coin))
      const to = this.$root.account.address

      return {
        currencies: {
          from: inputCurrency,
          to: outputCurrency,
        },
        currencyBalances: {
          from: ''
        }
      }
    },
    approve() {

    },
    handleSwap() {
      // const 
    }
  }

}